export default {
  //当某月的天数
  getDaysInOneMonth: function(date) {
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const d = new Date(year, month, 0);
    return d.getDate();
  },
  //向前空几个
  getMonthweek: function(date) {
    const year = date.getFullYear();
    const month = date.getMonth();
    const dateFirstOne = new Date(year, month, 1);
    return this.sundayStart
      ? dateFirstOne.getDay() == 0
        ? 7
        : dateFirstOne.getDay()
      : dateFirstOne.getDay() == 0
      ? 6
      : dateFirstOne.getDay() - 1;
  },
  /**
   * 获取当前日期上个月或者下个月
   */
  getOtherMonth: function(date, str = "nextMonth") {
    const timeArray = this.dateFormat(date).split("-");
    const year = timeArray[0];
    const month = timeArray[1];
    const day = timeArray[2];
    let year2 = year;
    let month2;
    if (str === "nextMonth") {
      month2 = parseInt(month) + 1;
      if (month2 == 13) {
        year2 = parseInt(year2) + 1;
        month2 = 1;
      }
    } else {
      month2 = parseInt(month) - 1;
      if (month2 == 0) {
        year2 = parseInt(year2) - 1;
        month2 = 12;
      }
    }
    let day2 = day;
    const days2 = new Date(year2, month2, 0).getDate();
    if (day2 > days2) {
      day2 = days2;
    }
    if (month2 < 10) {
      month2 = "0" + month2;
    }
    if (day2 < 10) {
      day2 = "0" + day2;
    }
    const t2 = year2 + "/" + month2 + "/" + day2;
    return new Date(t2);
  },
  //上个月末尾的一些日期
  getLeftArr: function(date) {
    let arr = [];
    const leftNum = this.getMonthweek(date);
    const num = this.getDaysInOneMonth(this.getOtherMonth(date, "preMonth")) - leftNum + 1;
    const preDate = this.getOtherMonth(date, "preMonth");
    let year = preDate.getFullYear();
    let month = preDate.getMonth();
    //上个月多少开始
    for (let i = 0; i < leftNum; i++) {
      let day = num + i;
      const nowTime = this.dateFormat(new Date(year, month, day));
      arr.push({
        id: num + i,
        date: nowTime,
        isToday: false,
        otherMonth: `preMonth`
      });
    }
    return arr;
  },
  //下个月末尾的一些日期
  getRightArr: function(date) {
    let arr = [];
    const nextDate = this.getOtherMonth(date, "nextMonth");
    const leftLength = this.getDaysInOneMonth(date) + this.getMonthweek(date);
    const _length = 7 - (leftLength % 7);
    let year = nextDate.getFullYear();
    let month = nextDate.getMonth();
    for (let i = 0; i < _length; i++) {
      let day = i + 1;
      const nowTime = this.dateFormat(new Date(year, month, day));
      arr.push({
        id: i + 1,
        date: nowTime,
        isToday: false,
        otherMonth: `nextMonth`
      });
    }
    return arr;
  },
  //format日期
  dateFormat: function(date) {
    date = typeof date === "string" ? new Date(date.replace("/-/g", "/")) : date;
    let year = date.getFullYear();
    let month = date.getMonth() + 1;
    let day = date.getDate();
    return year + "-" + (month < 10 ? "0" : "") + month + "-" + (day < 10 ? "0" : "") + day;
  },
  //获取某月的列表不包括上月和下月
  getMonthListNoOther: function(date) {
    let arr = [];
    const num = this.getDaysInOneMonth(date);
    const year = date.getFullYear();
    const month = date.getMonth();
    let toDay = this.dateFormat(new Date());

    for (let i = 0; i < num; i++) {
      let day = i + 1;
      let nowTime = this.dateFormat(new Date(year, month, day));
      arr.push({
        id: i + 1,
        date: nowTime,
        isToday: toDay === nowTime,
        otherMonth: `nowMonth`
      });
    }
    return arr;
  },
  //获取某月的列表 用于渲染
  getMonthList: function(date) {
    return [...this.getLeftArr(date), ...this.getMonthListNoOther(date), ...this.getRightArr(date)];
  },
  //默认是周一开始
  sundayStart: false
};
